<template>
  <div class="user-settings">
    <div class="settings-header">
      <h1></h1>
      <p></p>
    </div>
    
    <div class="settings-container">
      <!--  -->
      <div class="settings-tabs">
        <div 
          v-for="tab in tabs" 
          :key="tab.id"
          :class="['tab-item', { active: activeTab === tab.id }]"
          @click="activeTab = tab.id"
        >
          <i :class="['iconfont', tab.icon]"></i>
          <span>{{ tab.label }}</span>
        </div>
      </div>
      
      <!--  -->
      <div class="settings-content">
        <!--  -->
        <div v-if="activeTab === 'profile'" class="profile-settings">
          <h2></h2>
          <div class="profile-form-container">
            <div v-loading="loading" class="profile-form-wrapper">
              <div class="form-group avatar-group">
                <div class="avatar-label"></div>
                <div class="avatar-container">
                  <input
                    type="file"
                    ref="avatarFileInput"
                    accept="image/jpeg,image/png,image/gif"
                    @change="handleAvatarFileSelect"
                    class="avatar-file-input"
                    style="display: none;"
                  >
                  <img 
                    :src="user?.avatar || '/avatar.png'" 
                    :alt="user?.nickname || 'User Avatar'"
                    class="user-avatar"
                    @click="triggerAvatarFileSelect"
                  >
                  <div class="avatar-upload-hint" @click="triggerAvatarFileSelect"></div>
                </div>
              </div>

              <div class="form-row">
                <div class="form-group full-width">
                  <div class="form-label"></div>
                  <input 
                    type="text" 
                    v-model="editForm.nickname" 
                    placeholder="" 
                    class="form-input"
                    maxlength="20"
                  >
                </div>
              </div>


              
              <div class="form-row">
                <div class="form-group full-width">
                  <div class="form-label"></div>
                  <input 
                    type="email" 
                    v-model="editForm.email" 
                    placeholder="" 
                    class="form-input"
                    maxlength="100"
                  >
                </div>
              </div>



              <div class="form-actions">
                <button 
                  @click="handleUpdateProfile" 
                  class="btn btn-primary"
                  :disabled="!editForm.nickname.trim() || updating"
                >
                  <span v-if="updating">...</span>
                  <span v-else></span>
                </button>
              </div>
            </div>
          </div>
        </div>
        
        <!--  -->
        <div v-if="showAvatarModal" class="modal-overlay">
          <div class="modal-container">
            <div class="modal-header">
              <div class="modal-title"></div>
              <button class="close-btn" @click="closeAvatarModal"></button>
            </div>
            <div class="modal-body">
              <div class="avatar-upload-section">
                <img 
                  :src="avatarUrl || '/avatar.png'" 
                  :alt="''" 
                  class="preview-avatar"
                >
                <div class="uploader-container">
                  <ImageUploader 
                    v-model="avatarUrl" 
                    upload-type="avatar"
                  ></ImageUploader>
                </div>
              </div>
            </div>
            <div class="modal-footer">
              <button class="btn btn-secondary" @click="closeAvatarModal"></button>
              <button 
                class="btn btn-primary" 
                @click="handleUpdateAvatar"
                :disabled="!avatarUrl || updating"
              >
                <span v-if="updating">...</span>
                <span v-else></span>
              </button>
            </div>
          </div>
        </div>
        
        <!--  -->
        <div v-if="activeTab === 'password'" class="password-settings">
          <h2></h2>
          <div class="password-form-container">
            <div class="profile-form-wrapper">
            <div class="form-group avatar-group">
              <div class="avatar-label">&nbsp;</div>
              <div class="avatar-placeholder"></div>
            </div>
            
            <div class="form-description"></div>
            
            <form @submit.prevent="handleChangePassword" class="password-form">
              <div class="form-group">
                <label for="oldPassword"></label>
                <div class="input-with-icon">
                  
                  <input
                    type="password"
                    id="oldPassword"
                    v-model="passwordForm.oldPassword"
                    placeholder=""
                    class="form-input"
                    :class="{ error: passwordErrors.oldPassword }"
                    required
                  />
                </div>
                <span v-if="passwordErrors.oldPassword" class="error-message">{{ passwordErrors.oldPassword }}</span>
              </div>
              
              <div class="form-group">
                <label for="newPassword"></label>
                <div class="input-with-icon">
                  
                  <input
                    type="password"
                    id="newPassword"
                    v-model="passwordForm.newPassword"
                    placeholder=""
                    class="form-input"
                    :class="{ error: passwordErrors.newPassword }"
                    required
                  />
                </div>
                <span v-if="passwordErrors.newPassword" class="error-message">{{ passwordErrors.newPassword }}</span>
                <p class="password-hint">8</p>
              </div>
              
              <div class="form-group">
                <label for="confirmPassword"></label>
                <div class="input-with-icon">
                  
                  <input
                    type="password"
                    id="confirmPassword"
                    v-model="passwordForm.confirmPassword"
                    placeholder=""
                    class="form-input"
                    :class="{ error: passwordErrors.confirmPassword }"
                    required
                  />
                </div>
                <span v-if="passwordErrors.confirmPassword" class="error-message">{{ passwordErrors.confirmPassword }}</span>
              </div>
              
              <!--  -->
              <div class="form-row">
                <div class="form-group full-width password-placeholder">
                  <div class="form-label">&nbsp;</div>
                  <div class="form-placeholder"></div>
                </div>
              </div>
              
              <div class="form-row">
                <div class="form-group full-width password-placeholder">
                  <div class="form-label">&nbsp;</div>
                  <div class="form-placeholder"></div>
                </div>
              </div>
              
              <div class="form-actions">
                <button type="submit" class="btn btn-primary" :disabled="passwordLoading">
                    <div v-if="passwordLoading" class="loading-spinner small"></div>
                    {{ passwordLoading ? '...' : '' }}
                  </button>
              </div>
            </form></div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script setup lang="ts">
import { ref, reactive, onMounted, getCurrentInstance } from 'vue'
import { useUserStore } from '@/stores/user'
import type { User } from '@/types/user'
import ImageUploader from '@/components/ImageUploader.vue'
import { http, jsonBody, updatePhoto } from '@/api/http'
import { userApi } from '@/api/users'
import notificationService from '@/utils/notification'
import { uploadAvatar } from '@/api/upload'

// 
const notify = notificationService

// 
const tabs = [
  { id: 'profile', label: '', icon: 'icon-user' },
  { id: 'password', label: '', icon: 'icon-lock' }
]

// 
const activeTab = ref('profile')

// 
const userStore = useUserStore()
const user = ref<User | null>(null)
const loading = ref(true)
const updating = ref(false)

// 
const editForm = reactive({
  nickname: '',
  avatar: '',
  email: ''
})

// 
const newAvatarFile = ref<File | null>(null)

// 
const showAvatarModal = ref(false)
const avatarUrl = ref('')
const avatarFileInput = ref<HTMLInputElement | null>(null)

/**
 * 
 */
const loadUserProfile = async () => {
  try {
    loading.value = true
    // store
    const currentUser = userStore.user
    if (currentUser) {
      user.value = { ...currentUser }
      // 
      editForm.nickname = user.value.nickname || ''
      editForm.avatar = user.value.avatar || ''
      editForm.email = user.value.email || ''
    }
  } catch (error) {
    console.error(':', error)
  } finally {
    loading.value = false
  }
}

/**
 * 
 */
const triggerAvatarFileSelect = () => {
  avatarFileInput.value?.click()
}

/**
 * 
 */
const handleAvatarFileSelect = (event: Event) => {
  const target = event.target as HTMLInputElement
  const file = target.files?.[0]
  if (!file) return

  // 
  if (!file.type.startsWith('image/')) {
    notify.error('')
    return
  }

  // 10MB
  const maxSize = 10 * 1024 * 1024
  if (file.size > maxSize) {
    notify.error('10MB')
    return
  }

  // 
  newAvatarFile.value = file
  
  // URL
  const previewUrl = URL.createObjectURL(file)
  if (user.value) {
    user.value.avatar = previewUrl
  }
  
  // input
  if (target) {
    target.value = ''
  }
}

/**
 * 
 */
const closeAvatarModal = () => {
  showAvatarModal.value = false
  avatarUrl.value = user.value?.avatar || ''
}

/**
 * 
 */
const handleUpdateProfile = async () => {
  updating.value = true
  try {
    let avatarUrl = editForm.avatar || (user.value && user.value.avatar) || ''
    
    // URL
    if (newAvatarFile.value) {
      try {
        avatarUrl = await uploadAvatar(newAvatarFile.value)
        editForm.avatar = avatarUrl
        // 
        newAvatarFile.value = null
      } catch (error: any) {
        console.error(':', error)
        notify.error(error.message || '')
        return
      }
    }
    
    // 
    const updateData: any = {
      nickname: editForm.nickname,
      email: editForm.email
    }
    
    // avatar
    if (avatarUrl) {
      updateData.avatar = avatarUrl
    }
    
    // 
    if (!user.value || !user.value.id) {
      console.error('')
      notify.error('')
      return
    }
    
    // API
    const userId = user.value.id
    const response = await userApi.updateUserProfile(userId, updateData)
    
    if (response && response.code === 200) {
      // 
      if (user.value) {
        if (avatarUrl) {
          user.value.avatar = avatarUrl
        }
        user.value.nickname = editForm.nickname
        user.value.email = editForm.email
      }
      
        // store
        if (user.value) {
          userStore.$patch({ user: { ...user.value } })
        }
      
      // 
      notify.success('')
    } else {
      console.error(':', response?.message || '')
      notify.error('')
    }
  } catch (error) {
    console.error(':', error)
    notify.error('')
  } finally {
    updating.value = false
  }
}

/**
 * 
 */
const handleUpdateAvatar = async () => {
  updating.value = true
  try {
    if (avatarUrl.value && user.value) {
      // avatarUrl.value URL
      // ImageUploaderavatarUrl.valueURL
      // 
      let uploadedAvatarUrl = avatarUrl.value
      
      // avatarUrlblob URL
      if (avatarUrl.value.startsWith('blob:')) {
        // ImageUploader
        // 
        notify.error('')
        return
      }
      
      // 
      const updateData = {
        nickname: user.value.nickname,
        avatar: uploadedAvatarUrl,
        email: user.value.email
      }
      
      // API
      const userId = user.value.id
      const response = await userApi.updateUserProfile(userId, updateData)
      
      if (response && response.code === 200) {
        // 
        user.value.avatar = uploadedAvatarUrl
        editForm.avatar = uploadedAvatarUrl
        
        // store
        if (user.value) {
          userStore.$patch({ user: { ...user.value } })
        }
        
        // 
        notify.success('')
        
        closeAvatarModal()
      } else {
        console.error(':', response?.message || '')
        notify.error('')
      }
    }
  } catch (error) {
    console.error(':', error)
    notify.error('')
  } finally {
    updating.value = false
  }
}

// 
onMounted(() => {
  loadUserProfile()
})

// 
const passwordForm = reactive({
  oldPassword: '',
  newPassword: '',
  confirmPassword: ''
})

const passwordErrors = reactive({
  oldPassword: '',
  newPassword: '',
  confirmPassword: ''
})

const passwordLoading = ref(false)

/**
 * 
 * @param {string} password - 
 * @returns {string} 
 */
const validatePassword = (password: string): string => {
  // 8
  const hasLetter = /[a-zA-Z]/.test(password)
  const hasNumber = /[0-9]/.test(password)
  const isLongEnough = password.length >= 8
  
  if (!hasLetter || !hasNumber || !isLongEnough) {
    return '8'
  }
  return ''
}

/**
 * 
 */
const handleChangePassword = async () => {
  // 
  passwordErrors.oldPassword = ''
  passwordErrors.newPassword = ''
  passwordErrors.confirmPassword = ''
  
  // 
  if (!passwordForm.oldPassword) {
    passwordErrors.oldPassword = ''
    return
  }
  
  // 
  passwordErrors.newPassword = validatePassword(passwordForm.newPassword)
  if (passwordErrors.newPassword) {
    return
  }
  
  // 
  if (passwordForm.newPassword !== passwordForm.confirmPassword) {
    passwordErrors.confirmPassword = ''
    return
  }
  
  // 
  try {
    passwordLoading.value = true
    
    // 
    const changePasswordData = {
      oldPassword: passwordForm.oldPassword,
      newPassword: passwordForm.newPassword,
      confirmPassword: passwordForm.confirmPassword
    }
    
    // API/user/{id}/password
    const userId = user.value?.id
    if (!userId) {
      throw new Error('')
    }
    
    const response = await userApi.changePassword(userId, changePasswordData)
    
    if (response && response.code === 200) {
      // 
        notify.success('')
      
      // 
      passwordForm.oldPassword = ''
      passwordForm.newPassword = ''
      passwordForm.confirmPassword = ''
    } else {
      // 
      console.error(':', response?.message || '')
        notify.error(response?.message || '')
    }
  } catch (error) {
    console.error(':', error)
      notify.error('')
  } finally {
    passwordLoading.value = false
  }
}
</script>

<style scoped>
