}

.modal-body {
  padding: 24px;
}

.modal-footer {
  display: flex;
  justify-content: flex-end;
  gap: 10px;
  padding: 16px 24px;
  border-top: 1px solid #e8e8e8;
}

/*  */
.avatar-upload-section {
  text-align: center;
}

.preview-avatar {
  width: 120px;
