  border-radius: 50%;
  object-fit: cover;
  margin-bottom: 20px;
  border: 3px solid #f0f0f0;
}

.uploader-container {
  margin-top: 20px;
}

/*  */
.password-form-container {
  max-width: 600px;
  margin: 0 auto;
}

/*  */
.profile-form-container, .password-form-container {
  max-width: 600px;
  margin: 0 auto;
}

.form-description {
  color: #606266;
  margin-bottom: 20px;
  font-size: 14px;
}

.password-form .form-group {
  margin-bottom: 20px;
}

.password-form label {
  display: block;
  margin-bottom: 8px;
  font-weight: 500;
  color: #303133;
}

.password-form .form-input {
  width: 100%;
  padding: 10px 12px;
  border: 1px solid #dcdfe6;
  border-radius: 4px;
  font-size: 14px;
  transition: border-color 0.3s;
}

.password-form .form-input:focus {
  outline: none;
  border-color: #409eff;
}

.password-form .form-input.error {
  border-color: #f56c6c;
}

.error-message {
  color: #f56c6c;
  font-size: 12px;
  margin-top: 4px;
  display: block;
}

.password-hint {
  font-size: 12px;
  color: #909399;
  margin-top: 5px;
}

.form-actions {
  margin-top: 30px;
  display: flex;
  gap: 15px;
}

.btn {
  padding: 10px 20px;
  border-radius: 4px;
  font-size: 14px;
  cursor: pointer;
  transition: all 0.3s;
  border: none;
  display: inline-flex;
  align-items: center;
  justify-content: center;
  gap: 8px;
}

.btn-primary {
  background-color: #409eff;
  color: white;
}

.btn-primary:hover:not(:disabled) {
  background-color: #66b1ff;
}

.btn-primary:disabled {
  background-color: #c0c4cc;
  cursor: not-allowed;
}

.loading-spinner {
  width: 16px;
  height: 16px;
  border: 2px solid rgba(255, 255, 255, 0.3);
  border-radius: 50%;
  border-top-color: white;
  animation: spin 0.8s linear infinite;
}

.loading-spinner.small {
