import type { SearchQueryParams, SearchResponse, SuggestItem } from '@/types/search'
import { http } from '@/api/http'
import { fetchMoviesList, searchMovies } from './movies'
import { fetchTvShowsList } from './tvshows'

const API_BASE = '/api'

function buildQuery(params: Record<string, unknown>): string {
  const searchParams = new URLSearchParams()

  Object.entries(params).forEach(([key, value]) => {
    if (value === undefined || value === null || value === '') return
    if (Array.isArray(value)) {
      if (value.length > 0) searchParams.set(key, value.join(','))
    } else {
      searchParams.set(key, String(value))
    }
  })

  return searchParams.toString()
}

export async function search(params: SearchQueryParams, signal?: AbortSignal): Promise<SearchResponse> {
  // 
  console.log('[Search API] :', {
    type: params.type,
    q: params.q,
    page: params.page,
    pageSize: params.pageSize,
    // 
    tag: params.tag,
    year: params.year,
    ratingGte: params.ratingGte,
    actor: params.actor,
    award: params.award,
    genres: params.genres
  })
  
  // 
  const type = params.type || 'movie'
  const hasSearchKeyword = params.q && params.q.trim() !== ''
  
  // 
  if (type === 'movie') {
    // 
    if (!hasSearchKeyword) {
      const moviesData = await fetchMoviesList({
        page: params.page,
        size: params.pageSize,
        tag: params.tag || params.genres?.join(','), // taggenres
        year: params.year || undefined,
        rating: params.ratingGte || undefined,
        actor: params.actor || undefined,
        award: params.award || undefined
        // /api/movies/list  country  language 
      }, signal)
      
      // list
      console.log('[List] :', moviesData)
      console.log('[List] :', {
        page: params.page,
        size: params.pageSize,
        tag: params.tag || params.genres?.join(','),
        year: params.year,
        rating: params.ratingGte,
        actor: params.actor,
        award: params.award
      })
      
      // 
      return {
        items: moviesData.movies.map((m: any) => ({
          id: m.id,
          title: m.title,
          year: m.year,
          rating: m.rating,
          genres: Array.isArray(m.tags) ? m.tags : (m.tags ? [m.tags] : []),
          poster: m.poster,
          regions: m.country ? [m.country] : undefined
        })),
        total: moviesData.total,
        page: moviesData.page,
        pageSize: moviesData.size,
        hasMore: moviesData.hasMore !== undefined ? moviesData.hasMore : (moviesData.page * moviesData.size < moviesData.total),
        facets: undefined // facets
      }
    }
    
    // 
    // keyword params.q
    const keyword = (params.q || '').trim()
    if (!keyword) {
      //  keyword 
      //  hasSearchKeyword
      throw new Error('')
    }
    
    console.log('[Search] :', keyword)
    //  tag  tag tag  genres
    const tagValue = params.tag?.trim() || params.genres?.join(',') || undefined
    console.log('[Search] tag:', {
      'params.tag': params.tag,
      'params.genres': params.genres,
      'tag': tagValue
    })
    
    const searchParams = {
      page: params.page,
      size: params.pageSize,
      keyword: keyword, // keyword 
      tag: tagValue, // tag tagtaggenres
      year: params.year || undefined,
      rating: params.ratingGte || undefined,
      actor: params.actor?.trim() || undefined,
      award: params.award?.trim() || undefined
      // country  language API
    }
    console.log('[Search] :', searchParams)
    const moviesData = await searchMovies(searchParams, signal)
    
    // 
    return {
      items: moviesData.movies.map((m: any) => ({
        id: m.id,
        title: m.title,
        year: m.year,
        rating: m.rating,
        genres: Array.isArray(m.tags) ? m.tags : (m.tags ? [m.tags] : []),
        poster: m.poster,
        regions: m.country ? [m.country] : undefined
      })),
      total: moviesData.total,
      page: moviesData.page,
      pageSize: moviesData.size,
      hasMore: moviesData.hasMore !== undefined ? moviesData.hasMore : (moviesData.page * moviesData.size < moviesData.total),
      facets: undefined // facets
    }
  }

  // 
  if (type === 'tv') {
    // 
    if (hasSearchKeyword) {
      const keyword = (params.q || '').trim()
      if (!keyword) {
        throw new Error('')
      }
      
      console.log(`[Search] :`, keyword)
      const tagValue = params.tag?.trim() || params.genres?.join(',') || undefined
      
      const query = buildQuery({
        page: params.page,
        size: params.pageSize,
        keyword: keyword,
        tag: tagValue,
        year: params.year,
        rating: params.ratingGte,
        actor: params.actor?.trim() || undefined,
        award: params.award?.trim() || undefined
      })
      
      console.log(`[Search] URL: /tvshows/search?${query}`)
      const res = await http<{ code: number; data: any }>(`/tvshows/search${query ? `?${query}` : ''}`, { signal })
      const data = res.data
      
      // 
      // 1. data.items + data.total + data.page + data.size + data.hasMore
      // 2. data.tvshows + data.pagination.total + data.pagination.page + data.pagination.size + data.pagination.has_next
      const items = data.items || data.tvshows || []
      const pagination = data.pagination || {}
      
      return {
        items: items.map((item: any) => ({
          id: item.id,
          title: item.title,
          year: item.year,
          rating: item.rating,
          genres: Array.isArray(item.tags) ? item.tags : (Array.isArray(item.genres) ? item.genres : (item.tags ? [item.tags] : [])),
          poster: item.poster,
          regions: item.regions || undefined,
          highlight: item.highlight
        })),
        total: data.total || pagination.total || 0,
        page: data.page || pagination.page || params.page || 1,
        pageSize: data.size || pagination.size || params.pageSize || 24,
        hasMore: data.hasMore !== undefined ? data.hasMore : (pagination.has_next !== undefined ? pagination.has_next : false),
        facets: data.facets
      }
    } else {
      // 
      console.log(`[List] `)
      const tvshowsData = await fetchTvShowsList({
        page: params.page,
        size: params.pageSize,
        tag: params.tag || params.genres?.join(','),
        year: params.year,
        rating: params.ratingGte,
        actor: params.actor,
        award: params.award
      }, signal)
      
      console.log(`[List] :`, tvshowsData)
      const data = tvshowsData
      
      // 
      return {
        items: data.tvshows.map((item: any) => ({
          id: item.id,
          title: item.title,
          year: item.year,
          rating: item.rating,
          genres: Array.isArray(item.tags) ? item.tags : (Array.isArray(item.genres) ? item.genres : (item.tags ? [item.tags] : [])),
          poster: item.poster,
          regions: item.country ? [item.country] : item.regions || undefined
        })),
        total: data.total || 0,
        page: data.page || params.page || 1,
        pageSize: data.size || params.pageSize || 24,
        hasMore: data.hasMore !== undefined ? data.hasMore : ((data.page || 1) * (data.size || 24) < (data.total || 0)),
        facets: undefined
      }
    }
  }
  
  // 
  if (type === 'variety') {
    // 
    if (hasSearchKeyword) {
      const keyword = (params.q || '').trim()
      if (!keyword) {
        throw new Error('')
      }
      
      console.log(`[Search] :`, keyword)
      const tagValue = params.tag?.trim() || params.genres?.join(',') || undefined
      
      const query = buildQuery({
        page: params.page,
        size: params.pageSize,
        keyword: keyword,
        tag: tagValue,
        year: params.year,
        rating: params.ratingGte,
        actor: params.actor?.trim() || undefined,
        award: params.award?.trim() || undefined
      })
      
      console.log(`[Search] URL: /varieties/search?${query}`)
      const res = await http<{ code: number; data: any }>(`/varieties/search${query ? `?${query}` : ''}`, { signal })
      const data = res.data
      
      const items = data.items || data.varieties || []
      return {
        items: items.map((item: any) => ({
          id: item.id,
          title: item.title,
          year: item.year,
          rating: item.rating,
          genres: Array.isArray(item.tags) ? item.tags : (Array.isArray(item.genres) ? item.genres : (item.tags ? [item.tags] : [])),
          poster: item.poster,
          regions: item.regions || undefined,
          highlight: item.highlight
        })),
        total: data.total || data.pagination?.total || 0,
        page: data.page || data.pagination?.page || params.page || 1,
        pageSize: data.pageSize || data.pagination?.size || params.pageSize || 24,
        hasMore: data.hasMore !== undefined ? data.hasMore : (data.pagination?.has_next !== undefined ? data.pagination?.has_next : false),
        facets: data.facets
      }
    } else {
      // : /api/varieties/list
      console.log(`[List] `)
      const query = buildQuery({
        page: params.page,
        size: params.pageSize,
        tag: params.tag || params.genres?.join(','),
        year: params.year,
        rating: params.ratingGte,
        actor: params.actor,
        award: params.award
      })
      
      console.log(`[List] URL: /varieties/list?${query}`)
      const res = await http<{ code: number; data: any }>(`/varieties/list${query ? `?${query}` : ''}`, { signal })
      const data = res.data
      
      return {
        items: (data.varieties || []).map((item: any) => ({
          id: item.id,
          title: item.title,
          year: item.year,
          rating: item.rating,
          genres: Array.isArray(item.tags) ? item.tags : (Array.isArray(item.genres) ? item.genres : (item.tags ? [item.tags] : [])),
          poster: item.poster,
          regions: item.regions || undefined
        })),
        total: data.total || data.pagination?.total || 0,
        page: data.page || data.pagination?.page || params.page || 1,
        pageSize: data.pageSize || data.pagination?.size || params.pageSize || 24,
        hasMore: data.hasMore !== undefined ? data.hasMore : ((data.page || 1) * (data.pageSize || 24) < (data.total || 0)),
        facets: data.facets
      }
    }
  }
  
  // 
  return {
    items: [],
    total: 0,
    page: params.page || 1,
    pageSize: params.pageSize || 24,
    hasMore: false,
    facets: undefined
  }
}

export async function suggest(q: string, signal?: AbortSignal): Promise<SuggestItem[]> {
  const query = new URLSearchParams({ q })
  return http<SuggestItem[]>(`/suggest?${query.toString()}`, { signal })
}


