<template>
  <div id="app">
    <NavBar />
    <main class="main-content">
      <div class="router-view-wrapper">
        <router-view />
      </div>
    </main>
    <Footer />
    <!--  -->
    <Notification />
    <ConfirmDialog 
      :visible="confirmConfig.visible"
      :title="confirmConfig.title"
      :message="confirmConfig.message"
      :confirm-text="confirmConfig.confirmText"
      :cancel-text="confirmConfig.cancelText"
      :type="confirmConfig.type"
      :is-processing="confirmConfig.isProcessing"
      @confirm="confirmService.handleConfirm"
      @cancel="confirmService.handleCancel"
      @close="confirmService.close"
    />
  </div>
</template>

<script setup>
import { onMounted } from 'vue'
import NavBar from './components/NavBar.vue'
import Footer from './components/Footer.vue'
import Notification from './components/Notification.vue'
import ConfirmDialog from './components/ConfirmDialog.vue'
import { useUserStore } from './stores/user'
import { confirmService, confirmConfig } from './utils/confirm'

const userStore = useUserStore()

// 
onMounted(async () => {
  // 
  if (userStore.isLoggedIn) {
    return
  }
  
  // 
  const autoLoginSuccess = await userStore.autoLogin()
  
  //  session cookie
  if (!autoLoginSuccess) {
    await userStore.loadUserFromStorage()
  }
})
</script>

<style lang="scss">
#app {
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.main-content {
  flex: 1;
  padding-top: 70px; // 
  display: flex;
  flex-direction: column;
  min-height: 0;
}

.router-view-wrapper {
  flex: 1;
  display: flex;
  flex-direction: column;
  min-height: 0;
}
</style>
