              placeholder=""
              min="1"
            />
          </div>
        </div>

        <div class="form-group">
          <label></label>
          <input 
            v-model="form.country" 
            type="text" 
            placeholder=""
          />
        </div>

        <div class="form-group">
          <label></label>
          <input 
            v-model="form.language" 
            type="text" 
            placeholder=""
          />
        </div>

        <div class="form-group">
          <label></label>
          <div class="tag-input-group">
            <input 
              v-model="newTag" 
              type="text" 
              placeholder=""
              @keyup.enter.prevent="addTag"
              class="tag-input"
            />
            <button type="button" @click="addTag" class="add-tag-btn"></button>
          </div>
          <div class="tags-container">
            <span 
              v-for="(tag, index) in form.tags" 
              :key="index" 
              class="tag"
            >
              {{ tag }}
              <button type="button" @click="removeTag(index)" class="tag-remove"></button>
            </span>
          </div>
        </div>

        <div class="form-group">
          <label></label>
          <div class="select-with-search">
            <input 
