import { ref } from 'vue';

// 
export type ConfirmDialogType = 'warning' | 'danger' | 'info';

// 
export interface ConfirmOptions {
  title?: string;
  type?: ConfirmDialogType;
  confirmText?: string;
  cancelText?: string;
}

// 
export interface ConfirmConfig {
  visible: boolean;
  title: string;
  message: string;
  confirmText: string;
  cancelText: string;
  type: ConfirmDialogType;
  resolve?: (value: boolean) => void;
  reject?: (reason?: any) => void;
  isProcessing: boolean;
}

// 
const confirmConfig = ref<ConfirmConfig>({
  visible: false,
  title: '',
  message: '',
  confirmText: '',
  cancelText: '',
  type: 'warning',
  isProcessing: false
});

/**
 * 
 * confirm
 */
class ConfirmService {
  /**
   * 
   * @param message 
   * @param options 
   * @returns Promisetruefalse
   */
  confirm(message: string, options: ConfirmOptions = {}): Promise<boolean> {
    return new Promise((resolve, reject) => {
      confirmConfig.value = {
        visible: true,
        title: options.title || '',
        message,
        confirmText: options.confirmText || '',
        cancelText: options.cancelText || '',
        type: options.type || 'warning',
        resolve,
        reject,
        isProcessing: false
      };
    });
  }

  /**
   * 
   * @param message 
   * @param options 
   * @returns Promise
   */
  warning(message: string, options: Omit<ConfirmOptions, 'type'> = {}): Promise<boolean> {
    return this.confirm(message, { ...options, type: 'warning' });
  }

  /**
   * 
   * @param message 
   * @param options 
   * @returns Promise
   */
  danger(message: string, options: Omit<ConfirmOptions, 'type'> = {}): Promise<boolean> {
    return this.confirm(message, { ...options, type: 'danger' });
  }

  /**
   * 
   * @param message 
   * @param options 
   * @returns Promise
   */
  info(message: string, options: Omit<ConfirmOptions, 'type'> = {}): Promise<boolean> {
    return this.confirm(message, { ...options, type: 'info' });
  }

  /**
   * 
   */
  handleConfirm = (): void => {
    confirmConfig.value.isProcessing = true;
    
    // 
    setTimeout(() => {
      if (confirmConfig.value.resolve) {
        confirmConfig.value.resolve(true);
      }
      // thisclosethis
      this.close();
    }, 100);
  }

  /**
   * 
   */
  handleCancel = (): void => {
    if (confirmConfig.value.resolve) {
      confirmConfig.value.resolve(false);
    }
    // thisclosethis
    this.close();
  }

  /**
   * 
   */
  close(): void {
    confirmConfig.value = {
      visible: false,
      title: '',
      message: '',
      confirmText: '',
      cancelText: '',
      type: 'warning',
      isProcessing: false
    };
  }

  /**
   * 
   * @param isProcessing 
   */
  setProcessing(isProcessing: boolean): void {
    confirmConfig.value.isProcessing = isProcessing;
  }
}

// 
export const confirmService = new ConfirmService();

// 
export { confirmConfig };

// 
export default confirmService;