import { http } from './http';

/**
 * 
 */
export interface NotificationCountResponse {
  code: number;
  message: string;
  data: {
    unread: number;
  };
}

/**
 * 
 */
export interface NotificationFromUser {
  id: number;
  nickname: string;
  username: string;
  avatar: string | null;
}

/**
 * 
 */
export interface NotificationItem {
  id: number;
  title: string;
  content: string;
  type: string;
  isRead: boolean;
  read?: boolean;  //  read 
  createdAt: string;
  fromUser?: NotificationFromUser;  // 
  postId?: number | null;  // ID
  commentId?: number | null;  // ID
  parentCommentId?: number | null;  // ID
  [key: string]: any;
}

/**
 * 
 */
export interface NotificationListResponse {
  code: number;
  message: string;
  data: {
    notifications: NotificationItem[];
    total: number;
    unread: number;
  };
}

/**
 * 
 */
export interface MarkReadResponse {
  code: number;
  message: string;
  data?: any;
}

/**
 * API
 */
export const notificationApi = {
  /**
   * 
   * @returns 
   */
  getNotificationCount: async (): Promise<NotificationCountResponse> => {
    try {
      const response = await http<NotificationCountResponse>('/notifications/count');
      return response;
    } catch (error) {
      console.error(':', error);
      throw error;
    }
  },

  /**
   * 
   * @returns 
   */
  getNotificationList: async (): Promise<NotificationListResponse> => {
    try {
      const response = await http<NotificationListResponse>('/notifications/list?page=1&size=10000000');
      return response;
    } catch (error) {
      console.error(':', error);
      throw error;
    }
  },

  /**
   * 
   * @param notificationId ID
   * @returns 
   */
  markAsRead: async (notificationId: number): Promise<MarkReadResponse> => {
    try {
      const response = await http<MarkReadResponse>(`/notifications/${notificationId}/read`, {
        method: 'POST'
      });
      return response;
    } catch (error) {
      console.error(':', error);
      throw error;
    }
  },

  /**
   * 
   * @returns 
   */
  readAll: async (): Promise<MarkReadResponse> => {
    try {
      const response = await http<MarkReadResponse>('/notifications/readAll', {
        method: 'POST'
      });
      return response;
    } catch (error) {
      console.error(':', error);
      throw error;
    }
  },
};

