export interface MovieActor {
  id: number; // ID
  role: string; // 
  description: string; // 
}

export interface MovieSaveData {
  id?: number; // idid
  title: string;
  original_title?: string;
  year: number;
  tags: string[];
  director: number; // ID
  actors: MovieActor[]; // idroledescription
  poster: string;
  summary: string;
  awards?: number[]; // ID
  duration: number; // 
  country: string;
  language: string;
  trailer?: string;
  photos: string[];
}

//  - POST /movies/addbodyid
export async function saveMovie(movieData: MovieSaveData, signal?: AbortSignal): Promise<{ id: number }> {
  // POST /movies/addbodyidid
  const res = await http<{ code: number; data: { id: number } }>('/movies/add', {
    method: 'POST',
    body: jsonBody(movieData),
    signal
  })
  return res.data
}

