//  -  /api/movies/search 
export async function searchMovies(params: MoviesListQuery = {}, signal?: AbortSignal): Promise<MoviesListResponse> {
  // keyword 
  if (!params.keyword || !params.keyword.trim()) {
    throw new Error('keyword ')
  }
  
  const query = buildQuery({
    page: params.page,
    size: params.size,
    keyword: params.keyword.trim(), // keyword 
    tag: params.tag || params.genre, // taggenre
    year: params.year,
    rating: params.rating,
    actor: params.actor,
    award: params.award
    // country  language API
  })
  // : GET /api/movies/search
  const res = await http<{ code: number; data: any }>(`/movies/search${query ? `?${query}` : ''}`, { signal })
  const data = res.data
  // 
  return {
    movies: data.movies || [],
    total: data.pagination?.total || 0,
    page: data.pagination?.page || params.page || 1,
    size: data.pagination?.size || params.size || 10,
    hasMore: data.pagination?.has_next !== false
  }
}

