<template>
  <Teleport to="body">
    <Transition name="notification-panel">
      <div v-if="visible" class="notification-panel-overlay" @click="handleOverlayClick">
        <div class="notification-panel" @click.stop>
          <!--  -->
          <div class="notification-panel-header">
            <h3 class="panel-title"></h3>
            <div class="header-actions">
              <button 
                v-if="!loading && !error && notifications.length > 0"
                class="read-all-btn" 
                @click="handleReadAll"
                :disabled="markingAllRead || displayUnreadCount === 0"
              >
                <i class="icon-broom"></i>
                <span></span>
              </button>
              <button class="close-btn" @click="handleClose">
                <i class="icon-close"></i>
              </button>
            </div>
          </div>

          <!--  -->
          <div class="notification-tabs">
            <button
              v-for="tab in tabs"
              :key="tab.id"
              :class="['tab-item', { active: activeTab === tab.id }]"
              @click="activeTab = tab.id"
            >
              <span>{{ tab.label }}</span>
              <span v-if="tab.badge && tab.badge > 0" class="tab-badge">{{ tab.badge }}</span>
            </button>
          </div>

          <!--  -->
          <div class="notification-content">
            <div v-if="loading" class="loading-state">
              <div class="loading-spinner"></div>
              <p>...</p>
            </div>

            <div v-else-if="error" class="error-state">
              <i class="icon-error-circle"></i>
              <p>{{ error }}</p>
              <button class="retry-btn" @click="fetchNotifications"></button>
            </div>

            <div v-else-if="filteredNotifications.length === 0" class="empty-state">
              <i class="icon-bell"></i>
              <p></p>
            </div>

            <div v-else class="notification-list">
              <div
                v-for="notification in filteredNotifications"
                :key="notification.id"
                :class="['notification-item', { unread: !notification.isRead, read: notification.isRead }]"
              >
                <div class="notification-icon" :class="{ 'unread-icon': !notification.isRead }">
                  <i :class="getNotificationIcon(notification)"></i>
                </div>
                <div class="notification-body" @click="handleNotificationNavigation(notification)">
                  <div class="notification-title" :class="{ 'unread-title': !notification.isRead }">
                    {{ notification.title }}
                    <span v-if="!notification.isRead" class="unread-badge"></span>
                  </div>
                  <div class="notification-text">
                    <span v-if="notification.fromUser" class="notification-content">
                      <span 
                        class="user-name-link" 
                        @click.stop="handleUserClick(notification.fromUser.id)"
                      >
                        {{ notification.fromUser.nickname || notification.fromUser.username }}
                      </span>
                      <span class="notification-content-text">{{ getNotificationContentText(notification) }}</span>
                    </span>
                    <span v-else>{{ notification.content }}</span>
                  </div>
                  <div class="notification-time" :class="{ 'unread-time': !notification.isRead }">
                    <i class="icon-clock time-icon"></i>
                    {{ formatTime(notification.createdAt) }}
                  </div>
                </div>
                <div class="notification-actions">
                  <button
                    v-if="!notification.isRead"
                    class="mark-read-btn"
                    @click.stop="handleMarkAsRead(notification)"
                    title=""
                  >
                    <i class="icon-check"></i>
                  </button>
                  <div v-if="!notification.isRead" class="unread-dot"></div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </Transition>
  </Teleport>
</template>

<script setup lang="ts">
import { ref, computed, watch } from 'vue'
import { useRouter } from 'vue-router'
import { notificationApi, type NotificationItem } from '../api/notifications'

const router = useRouter()

interface Props {
  visible: boolean
  unreadCount?: number
}

const props = withDefaults(defineProps<Props>(), {
  unreadCount: 0
})

const emit = defineEmits<{
  close: []
  updateUnread: [count: number]
}>()

//  - 
const tabs = computed(() => [
  { id: 'unread', label: '', badge: props.unreadCount },
  { id: 'read', label: '', badge: null }
])

const activeTab = ref('unread')
const notifications = ref<NotificationItem[]>([])
const loading = ref(false)
const error = ref<string | null>(null)
const markingAllRead = ref(false)

// 
const actualUnreadCount = computed(() => {
  return notifications.value.filter(n => !n.isRead).length
})

// 
const displayUnreadCount = computed(() => {
  // 
  if (notifications.value.length > 0) {
    return actualUnreadCount.value
  }
  //  props
  return props.unreadCount
})

//  - 
const filteredNotifications = computed(() => {
  console.log(':', activeTab.value)
  console.log(':', notifications.value.map(n => ({ id: n.id, title: n.title, isRead: n.isRead })))
  
  if (activeTab.value === 'read') {
    const readNotifications = notifications.value.filter(n => n.isRead)
    console.log(' - :', { total: notifications.value.length, read: readNotifications.length })
    return readNotifications
  }
  // 
  const unreadNotifications = notifications.value.filter(n => !n.isRead)
  console.log(' - :', { total: notifications.value.length, unread: unreadNotifications.length })
  return unreadNotifications
})

/**
 * 
 */
const fetchNotifications = async () => {
  if (!props.visible) return
  
  loading.value = true
  error.value = null
  
  try {
    const response = await notificationApi.getNotificationList()
    if (response && response.code === 200) {
      let notificationsData = response.data?.notifications || []
      
      // 
      notificationsData = notificationsData.map((notification: any) => {
        //  read  isRead
        if (notification.read !== undefined && notification.isRead === undefined) {
          notification.isRead = notification.read
        }
        //  createTime  createdAt
        if (notification.createTime !== undefined && notification.createdAt === undefined) {
          notification.createdAt = notification.createTime
        }
        //  title  title
        if (!notification.title) {
          notification.title = getNotificationTitle(notification.type)
        }
        return notification
      })
      
      notifications.value = notificationsData
      
      // 
      const unreadCount = response.data?.unread || 0
      emit('updateUnread', unreadCount)
      
      // 
      console.log(':', {
        total: notifications.value.length,
        unread: notifications.value.filter(n => !n.isRead).length,
        read: notifications.value.filter(n => n.isRead).length,
        firstFew: notifications.value.slice(0, 3).map(n => ({ id: n.id, title: n.title, isRead: n.isRead }))
      })
      
      // 
      if (notifications.value.length > 0) {
        console.log(':', notifications.value[0])
        console.log(' - isRead:', notifications.value[0].isRead, 'read:', (notifications.value[0] as any).read)
      }
    }
  } catch (err: any) {
    console.error(':', err)
    error.value = err.message || ''
  } finally {
    loading.value = false
  }
}

/**
 * 
 */
const getNotificationTitle = (type: string | undefined): string => {
  if (!type) return ''
  
  const typeUpper = type.toUpperCase()
  switch (typeUpper) {
    case 'FOLLOW':
      return ''
    case 'REPLY_POST':
      return ''
    case 'REPLY_COMMENT':
      return ''
    case 'LIKE_POST':
      return ''
    case 'LIKE_COMMENT':
      return ''
    default:
      return ''
  }
}

/**
 * 
 */
const getNotificationIcon = (notification: NotificationItem): string => {
  // 
  if (notification.type) {
    const typeUpper = notification.type.toUpperCase()
    switch (typeUpper) {
      case 'FOLLOW':
        return 'icon-users'
      case 'REPLY_POST':
      case 'REPLY_COMMENT':
        return 'icon-comment'
      case 'LIKE_POST':
      case 'LIKE_COMMENT':
        return 'icon-like'
      default:
        return 'icon-bell'
    }
  }
  
  // 
  return 'icon-bell'
}

/**
 * 
 */
const formatTime = (timeStr: string | undefined): string => {
  if (!timeStr) {
    return ''
  }
  
  const date = new Date(timeStr)
  // 
  if (isNaN(date.getTime())) {
    return ''
  }
  
  const now = new Date()
  const diff = now.getTime() - date.getTime()
  
  // ""
  if (diff < 0) {
    return ''
  }
  
  const days = Math.floor(diff / (1000 * 60 * 60 * 24))
  const hours = Math.floor(diff / (1000 * 60 * 60))
  const minutes = Math.floor(diff / (1000 * 60))

  if (days > 0) {
    return `${days} `
  } else if (hours > 0) {
    return `${hours} `
  } else if (minutes > 0) {
    return `${minutes} `
  } else {
    return ''
  }
}

/**
 * 
 */
const getNotificationContentText = (notification: NotificationItem): string => {
  if (!notification.fromUser || !notification.content) {
    return notification.content || ''
  }
  
  const userName = notification.fromUser.nickname || notification.fromUser.username
  //  content 
  if (notification.content.startsWith(userName)) {
    return notification.content.substring(userName.length).trim()
  }
  
  return notification.content
}

/**
 * 
 */
const handleUserClick = (userId: number) => {
  // 
  emit('close')
  // 
  router.push(`/user/${userId}`)
}

/**
 * 
 */
const handleMarkAsRead = async (notification: NotificationItem) => {
  if (notification.isRead) {
    return
  }

  try {
    //  API 
    const response = await notificationApi.markAsRead(notification.id)
    
    if (response && response.code === 200) {
      // 
      const index = notifications.value.findIndex(n => n.id === notification.id)
      if (index !== -1) {
        notifications.value[index].isRead = true
        notifications.value[index].read = true
      }
      
      // 
      const newUnreadCount = Math.max(0, props.unreadCount - 1)
      emit('updateUnread', newUnreadCount)
    }
  } catch (error) {
    console.error(':', error)
  }
}

/**
 * 
 */
const handleNotificationNavigation = (notification: NotificationItem) => {
  // 
  emit('close')

  //  postId
  if (notification.postId) {
    const postId = notification.postId
    const query: Record<string, string> = {}
    
    //  commentId query 
    if (notification.commentId) {
      query.commentId = notification.commentId.toString()
    }
    
    //  parentCommentId
    if (notification.parentCommentId) {
      query.parentCommentId = notification.parentCommentId.toString()
    }
    
    router.push({
      path: `/post/${postId}`,
      query: query
    })
  } else if (notification.fromUser) {
    //  postId  fromUser
    handleUserClick(notification.fromUser.id)
  }
}

/**
 * 
 */
const handleReadAll = async () => {
  if (markingAllRead.value || displayUnreadCount.value === 0) {
    return
  }

  markingAllRead.value = true
  
  try {
    const response = await notificationApi.readAll()
    
    if (response && response.code === 200) {
      // 
      notifications.value.forEach(notification => {
        notification.isRead = true
        notification.read = true
      })
      
      // 0
      emit('updateUnread', 0)
    }
  } catch (error) {
    console.error(':', error)
    // 
  } finally {
    markingAllRead.value = false
  }
}

/**
 * 
 */
const handleClose = () => {
  emit('close')
}

/**
 * 
 */
const handleOverlayClick = () => {
  emit('close')
}

//  visible 
watch(() => props.visible, (newVal) => {
  if (newVal) {
    fetchNotifications()
  }
})
</script>

<style lang="scss" scoped>
.notification-panel-overlay {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.5);
  display: flex;
  align-items: flex-start;
  justify-content: flex-end;
  z-index: 10000;
  padding: 20px;
  backdrop-filter: blur(4px);
}

.notification-panel {
  background: #1f2937;
  border-radius: 16px;
  width: 100%;
  max-width: 420px;
  max-height: calc(100vh - 40px);
  display: flex;
  flex-direction: column;
  box-shadow: 0 20px 60px rgba(0, 0, 0, 0.3);
  overflow: hidden;
  animation: slideIn 0.3s ease-out;
}

@keyframes slideIn {
  from {
    transform: translateX(100%);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}

.notification-panel-header {
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 20px 24px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  background: #111827;

  .panel-title {
    margin: 0;
    font-size: 20px;
    font-weight: 600;
    color: #ffffff;
  }

  .header-actions {
    display: flex;
    align-items: center;
    gap: 8px;
  }

  .read-all-btn {
    display: flex;
    align-items: center;
    gap: 6px;
    padding: 6px 12px;
    border: none;
    background: rgba(249, 115, 22, 0.2);
    border-radius: 6px;
    color: var(--primary-color, #f97316);
    font-size: 13px;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.2s ease;

    &:hover:not(:disabled) {
      background: rgba(249, 115, 22, 0.3);
      transform: translateY(-1px);
    }

    &:disabled {
      opacity: 0.5;
      cursor: not-allowed;
    }

    .icon-broom {
      font-family: inherit !important; //  emoji
    }
    
    i {
      font-size: 14px;
    }
  }

  .close-btn {
    width: 32px;
    height: 32px;
    border: none;
    background: rgba(255, 255, 255, 0.1);
    border-radius: 8px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    color: #9ca3af;
    transition: all 0.2s ease;

    &:hover {
      background: rgba(255, 255, 255, 0.2);
      color: #ffffff;
    }

    i {
      font-size: 18px;
    }
  }
}

.notification-tabs {
  display: flex;
  padding: 0 24px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  background: #111827;
  gap: 8px;

  .tab-item {
    position: relative;
    padding: 12px 16px;
    border: none;
    background: none;
    color: #9ca3af;
    font-size: 14px;
    font-weight: 500;
    cursor: pointer;
    transition: all 0.2s ease;
    border-bottom: 2px solid transparent;
    margin-bottom: -1px;
    display: flex;
    align-items: center;
    gap: 8px;

    &:hover {
      color: #ffffff;
    }

    &.active {
      color: #ffffff;
      border-bottom-color: var(--primary-color, #f97316);
    }

    .tab-badge {
      background: #ef4444;
      color: white;
      font-size: 11px;
      font-weight: 600;
      padding: 2px 6px;
      border-radius: 10px;
      min-width: 18px;
      height: 18px;
      display: flex;
      align-items: center;
      justify-content: center;
    }
  }
}

.notification-content {
  flex: 1;
  overflow-y: auto;
  padding: 8px 0;
}

.loading-state,
.error-state,
.empty-state {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  padding: 60px 24px;
  color: #9ca3af;
  text-align: center;

  i {
    font-size: 48px;
    margin-bottom: 16px;
    opacity: 0.5;
  }

  p {
    margin: 8px 0 0 0;
    font-size: 14px;
  }
}

.loading-spinner {
  width: 40px;
  height: 40px;
  border: 3px solid rgba(255, 255, 255, 0.1);
  border-top-color: var(--primary-color, #f97316);
  border-radius: 50%;
  animation: spin 0.8s linear infinite;
  margin-bottom: 16px;
}

@keyframes spin {
  to {
    transform: rotate(360deg);
  }
}

.error-state {
  .retry-btn {
    margin-top: 16px;
    padding: 8px 16px;
    background: var(--primary-color, #f97316);
    color: white;
    border: none;
    border-radius: 6px;
    cursor: pointer;
    font-size: 14px;
    transition: all 0.2s ease;

    &:hover {
      opacity: 0.9;
      transform: translateY(-1px);
    }
  }
}

.notification-list {
  display: flex;
  flex-direction: column;
}

.notification-item {
  display: flex;
  align-items: flex-start;
  gap: 12px;
  padding: 16px 24px;
  cursor: pointer;
  transition: all 0.2s ease;
  border-bottom: 1px solid rgba(255, 255, 255, 0.05);
  position: relative;

  &:hover {
    background: rgba(255, 255, 255, 0.05);
    transform: translateX(2px);
  }

  &.unread {
    background: rgba(249, 115, 22, 0.08);
    border-left: 3px solid var(--primary-color, #f97316);
    padding-left: 21px;

    &:hover {
      background: rgba(249, 115, 22, 0.15);
    }
  }

  &.read {
    opacity: 0.8;
    
    &:hover {
      opacity: 0.9;
    }
  }

  .notification-icon {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    background: rgba(255, 255, 255, 0.1);
    display: flex;
    align-items: center;
    justify-content: center;
    flex-shrink: 0;
    transition: all 0.2s ease;

    i {
      font-size: 20px;
      color: #9ca3af;
      transition: all 0.2s ease;
    }

    &.unread-icon {
      background: rgba(249, 115, 22, 0.2);
      
      i {
        color: var(--primary-color, #f97316);
        animation: pulse 2s infinite;
      }
    }
  }

  @keyframes pulse {
    0%, 100% { transform: scale(1); }
    50% { transform: scale(1.1); }
  }

  .notification-body {
    flex: 1;
    min-width: 0;
    cursor: pointer;

    .notification-title {
      font-size: 15px;
      font-weight: 600;
      color: #ffffff;
      margin-bottom: 4px;
      line-height: 1.4;
      display: flex;
      align-items: center;
      gap: 8px;

      &.unread-title {
        color: #fbbf24;
        font-weight: 700;
      }

      .unread-badge {
        background: #ef4444;
        color: white;
        font-size: 10px;
        font-weight: 700;
        padding: 2px 6px;
        border-radius: 10px;
        text-transform: uppercase;
        letter-spacing: 0.5px;
        animation: blink 1.5s infinite;
      }

      @keyframes blink {
        0%, 100% { opacity: 1; }
        50% { opacity: 0.7; }
      }
    }

    .notification-text {
      font-size: 13px;
      color: #9ca3af;
      line-height: 1.5;
      margin-bottom: 8px;
      display: -webkit-box;
      -webkit-line-clamp: 2;
      -webkit-box-orient: vertical;
      overflow: hidden;

      .notification-content {
        display: inline;
      }

      .user-name-link {
        color: var(--primary-color, #f97316);
        font-weight: 600;
        cursor: pointer;
        transition: all 0.2s ease;
        text-decoration: none;

        &:hover {
          color: #fb923c;
          text-decoration: underline;
        }

        &:active {
          color: #ea580c;
        }
      }

      .notification-content-text {
        color: #9ca3af;
      }
    }

    .notification-time {
      font-size: 12px;
      color: #6b7280;
      display: flex;
      align-items: center;
      gap: 6px;

      &.unread-time {
        color: #f59e0b;
        font-weight: 500;
      }

      .time-icon {
        font-size: 10px;
        opacity: 0.7;
      }
    }
  }

  .notification-actions {
    display: flex;
    align-items: center;
    gap: 8px;
    flex-shrink: 0;
  }

  .mark-read-btn {
    width: 28px;
    height: 28px;
    border: none;
    background: rgba(249, 115, 22, 0.2);
    border-radius: 6px;
    display: flex;
    align-items: center;
    justify-content: center;
    cursor: pointer;
    color: var(--primary-color, #f97316);
    transition: all 0.2s ease;
    opacity: 0;
    transform: scale(0.9);

    i {
      font-size: 14px;
    }

    &:hover {
      background: rgba(249, 115, 22, 0.3);
      transform: scale(1);
    }

    &:active {
      transform: scale(0.95);
    }
  }

  &:hover .mark-read-btn {
    opacity: 1;
    transform: scale(1);
  }

  .unread-dot {
    width: 8px;
    height: 8px;
    border-radius: 50%;
    background: #ef4444;
    flex-shrink: 0;
    margin-top: 6px;
  }
}

// 
.notification-panel-enter-active,
.notification-panel-leave-active {
  transition: opacity 0.3s ease;
}

.notification-panel-enter-active .notification-panel,
.notification-panel-leave-active .notification-panel {
  transition: transform 0.3s ease;
}

.notification-panel-enter-from,
.notification-panel-leave-to {
  opacity: 0;
}

.notification-panel-enter-from .notification-panel,
.notification-panel-leave-to .notification-panel {
  transform: translateX(100%);
}
</style>

