    <div class="movie-form-modal">
      <div class="modal-header">
        <h2>{{ isEdit ? '' : '' }}</h2>
        <button class="close-btn" @click="handleCancel"></button>
      </div>
      
      <form @submit.prevent="handleSubmit" class="movie-form">
        <div class="form-group">
          <label> <span class="required">*</span></label>
          <input 
            v-model="form.title" 
            type="text" 
            required 
            placeholder=""
          />
        </div>

        <div class="form-group">
          <label></label>
          <input 
            v-model="form.original_title" 
            type="text" 
            placeholder=""
          />
        </div>

        <div class="form-row">
          <div class="form-group">
            <label> <span class="required">*</span></label>
            <input 
              v-model.number="form.year" 
              type="number" 
              required 
              placeholder="2024"
              min="1900"
              max="2099"
            />
          </div>

          <div class="form-group">
            <label></label>
            <input 
              v-model.number="form.episodes" 
              type="number" 
              placeholder=""
              min="1"
            />
          </div>
        </div>

        <div class="form-row">
          <div class="form-group">
            <label></label>
            <input 
              v-model="form.country" 
              type="text" 
              placeholder=""
            />
          </div>

          <div class="form-group">
            <label></label>
            <input 
              v-model="form.language" 
              type="text" 
              placeholder=""
            />
          </div>
        </div>

        <div class="form-group">
          <label></label>
          <div class="tags-input">
            <div v-if="form.tags.length > 0" class="tags-list">
              <span v-for="(tag, index) in form.tags" :key="index" class="tag-item">
                {{ tag }}
                <button type="button" @click="removeTag(index)" class="tag-remove"></button>
              </span>
            </div>
            <div class="tags-input-row">
              <input 
                v-model="newTag" 
                type="text" 
                placeholder=""
                @keyup.enter="addTag"
                class="tag-input"
              />
              <button type="button" @click="addTag" class="add-tag-btn"></button>
            </div>
          </div>
        </div>

        <div class="form-group">
          <label></label>
          <div class="select-with-search">
            <input 
              v-model="hostSearch" 
              type="text" 
              placeholder="..."
              @input="searchHost"
              class="search-input"
            />
            <select v-model.number="form.host" class="select-input">
              <option :value="0"></option>
              <option v-for="actor in hostOptions" :key="actor.id" :value="actor.id">
                {{ actor.name }}
              </option>
            </select>
          </div>
        </div>

        <div class="form-group">
          <label></label>
          <div class="select-with-search">
            <input 
              v-model="guestSearch" 
              type="text" 
              placeholder="..."
              @input="searchGuest"
              class="search-input"
            />
            <div class="multi-select">
              <select 
                v-model="selectedGuest" 
                @change="addGuest"
                class="select-input"
              >
                <option :value="0"></option>
                <option v-for="actor in guestOptions" :key="actor.id" :value="actor.id">
                  {{ actor.name }}
                </option>
              </select>
              <div v-if="form.guests.length > 0" class="guests-list">
                <div v-for="(guest, index) in form.guests" :key="index" class="guest-item">
                  <div class="guest-header">
                    <span class="guest-name">{{ getGuestName(guest.id) }}</span>
                    <button type="button" @click="removeGuest(index)" class="item-remove"></button>
                  </div>
                  <div class="guest-fields">
                    <div class="guest-field">
                      <label></label>
                      <input 
                        v-model="guest.role" 
                        type="text" 
                        placeholder=""
                        class="guest-input"
                      />
                    </div>
                    <div class="guest-field">
                      <label></label>
                      <input 
                        v-model="guest.description" 
                        type="text" 
                        placeholder=""
                        class="guest-input"
                      />
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>

        <div class="form-group">
          <label>URL</label>
          <ImageUploader
            v-model="form.poster"
            placeholder=""
            upload-type="image"
            button-text=""
            :disable-url-input="true"
          />
        </div>

        <div class="form-group">
          <label></label>
          <textarea 
            v-model="form.summary" 
            rows="4"
            placeholder=""
          ></textarea>
        </div>

