          <label>URL</label>
          <div class="photos-input">
            <div v-if="form.photos.length > 0" class="photos-list">
              <div v-for="(photo, index) in form.photos" :key="index" class="photo-item">
                <ImageUploader
                  v-model="form.photos[index]"
                  placeholder=""
                  upload-type="image"
                  button-text=""
                  :disable-url-input="true"
                />
                <button type="button" @click="removePhoto(index)" class="photo-remove"></button>
              </div>
            </div>
            <button type="button" @click="addPhoto" class="add-photo-btn">+ URL</button>
          </div>
